@keyframes twinkle {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.news_page .news_page_bottom .hidden .autotext {
  color: white;
}
@keyframes anime_textup {
  0% {
    top: 0;
  }
  20% {
    top: -0.2rem;
  }
  40% {
    top: 0;
  }
  60% {
    top: 0;
  }
  80% {
    top: 0;
  }
  100% {
    top: 0;
  }
}
